*{
  margin: 0;
  padding: 0;
}
body {
  background-image: url("./herbs.jpg");
  background-color: #cccccc;
  height: 100%; 
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

 }
div.container {
  position: relative;
  width: 800px;
  height: 800px;
  left: auto;
  top: 50px;
}
.Install-App{
  position: absolute;
  margin-left: 125%;
  cursor: pointer;
  font-family: arial;
  font-size: 18pt;
  text-align: right;
  color: white;
}
div.heading h1{
  position: absolute;
  color: white;
  margin-top: -7%;

  left: -200px;
  width: 1200px;
  height: 50px;
  padding: 20px;
  background: none;
  text-align: center;
  font-size: 500%;
}
div.Announcement h1{
  position: absolute;
  left: -200px;
  width: 1200px;
  height: 50px;
  top: 33px;
  padding: 7px;
  color: black;
  background: white;
  text-align: center;
 
}
.Title h1 span{
  color: green;
}
div.Title h1{
  position: absolute;
  font-weight: 700;
  left: -200px;
  width: 1200px;
  top: 100px;
  height: 50px;
  padding: 7px;
  background: white;
  box-sizing: border-box;
  text-align: center;
}

div.model-box1 {
  position: absolute;
  left: -200px;
  top: 190px;
  width: 550px;
  height: 320px;
  background: white
} 

div.model-box2 {
  position: absolute;
  left: 450px;
  top: 190px;
  width: 550px;
  height: 320px;
  background: white
}

div.model-box1 hr, div.model-box2 hr { 
 
  display: block;
  margin-top: 25px;
  margin-bottom: 0.5em;
  margin-left: 10px;
  margin-right: 10px;
  height: 3px;
  background-color: black;
  border: none;
}

div.model-box1 .title-box1{
  color: black;
  text-align: center
}

.name{
  width: 30%;
  text-align: right;
  color: black; 
  
}
.model-box1 .box-a{
background-color: white;
  height: 20px;
  top: -20px;
  margin-left: 260px;
  position:relative;
  border-style: solid;
  border-color: black;
}

.model-box1 .box-b{
background-color: green;
  height: 20px;
